body {
    padding: 10px;
    font-family: Arial, Helvetica, sans-serif;
    color: white;
    background: rgb(69, 77, 85);
}

ul li {
    display: inline;
    margin: 10px;    
}

ul {
    list-style-type: none;
}

.scrollBar {
    -ms-overflow-style: none;  /* Internet Explorer 10+ */
    scrollbar-width: none;  /* Firefox */
    max-height: 115px;
    overflow-y: auto;
    scrollbar-width: none;
}
.scrollBar::-webkit-scrollbar { 
    display: none;  /* Safari and Chrome */
}

.iconOnHover {
    color: white;
}

.iconOnHover:hover{
    color: #0275d8;
}

.fa-plus {
    color: white
}

.fa-plus:hover {
    color: #5cb85c
}

.fa-minus {
    color: white
}

.fa-minus:hover {
    color: #d9534f
}

.modalElement {
    background: #343a40; 
    width: fit-content;
    padding: 5px;
}


